@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width: 1280px) {
    @content;
  }
}
@mixin onSmallDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}
@mixin onTablet {
  @media (min-width: 640px) {
    @content;
  }
}
@mixin onMobile {
  @media (max-width: 639px) {
    @content;
  }
}
